
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Icon from 'vux/src/components/icon/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {Modules, request} from '../utils/HttpRequest/request';
import sessionMenagement from '../utils/StorageManagement';
import {postData} from '../utils/hexiao';
import { ossPrefix } from './../config'
var JsBarcode = require('jsbarcode')

export default {
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Cell,
Flexbox,
FlexboxItem,
AlertModule,
Confirm,
Icon,
AlertPlugin
},
data() {
return {


converted: false,
show: false,
couponType: 2,
status: 0,
coupon: '',
brandName: '',
validityDate: '',
startTime: '',
endTime: '',
desc: '：',
condition: '指定商品',
add: false,
added: false,
pay: false,
dataDetail: '',
briefTitle: '',
member_id: '',
coupon_code: '',
brandIcon: '',
goods_url: [],
goods_pic: '',
barCode: '',
source:1,
}
},
methods: {

createMember () {
JsBarcode("#barcode", this.barCode, {
width: 1.8,
height: 70
})
},
onConfirm(msg) {
console.log('确定', msg);

this.show = false;
this.$router.push({path: '/coupons'});
},

addSite() {
console.log('添加收获地址');
this.added = true;

},
exchange() {
console.log('点击了商品券线上兑换');


this.$router.push({path: '/goodsExchange',query:{dataDetail :this.dataDetail}});
postData(this.dataDetail)
},
goExchange() {
if (this.added) {
this.show = true;
this.briefTitle = '兑换成功'
} else {
AlertModule.show({
title: '提示',
content: '请先添加收货地址',
})
}
},
payment() {
AlertModule.show({

content: `<img style="width: 100%;height: 100%" src="${ossPrefix}btn_ok.png">
<p>恭喜您，已成功兑换！</p><p>请注意查收货品！</p>`,

});
},

getDetails() {
return new Promise(resolve =>{
this.goods_url=[];
request({
method: 'get',
url: Modules.COUPONS + '/coupon/detail?member_id=' + this.member_id + '&coupon_code=' + this.coupon_code,
config: {
headers: {
'authorization': 'member YsfrkQGy21BCIvP2aLlqVZjHz9lpLaDfsKldpUmFGEsXRhx4ycd5uOBPS42cm3Yu'
}
},
}).then(res => {
console.log('商品券详情', res.data.data);
if (res.data.data) {
let msg = res.data.data;
this.brandName = msg.brand_name;
this.desc = msg.remark;
this.coupon = msg.coupon_name;
this.brandIcon = msg.brand_icon;
this.barCode=msg.coupon_code;
this.startTime = msg.start_time;
this.endTime = msg.end_time;
this.status = msg.status;
msg.goods_url.map(item => {
this.goods_url.push(item);

});
resolve();

this.createMember()
} else {
console.log('无卡券详情！');
}
}, err => {
console.log('查询卡券详情失败，请重试！');
})
})
},
},
mounted() {
this.dataDetail  = this.$route.query.dataDetail;
this.source  = this.$route.query.source;

this.phone = sessionMenagement.getPhone();

this.member_id = sessionMenagement.getMemberId();
this.coupon_code = localStorage.getItem('coupon_code');
this.getDetails().then(res=> {
if(this.source==0){
this.exchange();
}
});
}

}
