
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'

import {Modules, request} from '../utils/HttpRequest/request';
import sessionMenagement from '../utils/StorageManagement';
import {emitChengxingApi,postData} from '../utils/hexiao';

export default {
components: {
XHeader,
XButton,
Cell,
Flexbox
},
data() {
return {
status: 0,
couponType: 3,
brandName: '',
coupon: '',
desc: '',
brandIcon: '',
goods_url: [],
goods_pic: '',
showExchange: true,
dataDetail: {},
goBack: true,
member_id: '',
phone: '',
source: 1,

type:1,
sign:'',

company_id:1
}
},
methods: {
back() {
console.log('红包券返回');
this.showExchange = true
},

getDetails() {
this.goods_url = [];
return new Promise(resolve=>{
request({
method: 'get',
url: Modules.COUPONS + '/coupon/detail?member_id=' + this.member_id + '&coupon_code='+this.coupon_code,
config: {
headers: {
'authorization': 'member YsfrkQGy21BCIvP2aLlqVZjHz9lpLaDfsKldpUmFGEsXRhx4ycd5uOBPS42cm3Yu'
}
},
}).then(res => {

if (res.data.data) {
let msg = res.data.data;
this.brandName = msg.brand_name;
this.desc = msg.remark;
this.coupon = msg.coupon_name;
this.brandIcon = msg.brand_icon;
this.status = msg.status;
msg.goods_url.map(item => {
this.goods_url.push(item);

});
resolve();
} else {
console.log('无卡券详情！');
}
}, err => {
console.log('查询卡券详情失败，请重试！');
})
})
},


exchange() {
console.log('this.source>>',this.source);
console.log('点击了红包券领取说明');
if (!this.phone) {
this.$router.push({path: '/bindMobile',query:{type :1}})
} else {
this.showExchange = false;
let params = {
"member_id": this.member_id,
"coupon_code": this.coupon_code,
"mobile": this.phone,
"company_id": this.company_id
};
request({
url: Modules.COUPONS + '/lottery/bind',
params: params,
config: {
headers: {
'authorization': 'member YsfrkQGy21BCIvP2aLlqVZjHz9lpLaDfsKldpUmFGEsXRhx4ycd5uOBPS42cm3Yu'
}
},
}).then(res => {
console.log('红包券领取说明返回信息', res.data.data);
}, err => {
console.log('红包券领取失败，请重试！', err);
})

};
postData(this.dataDetail)
},

},
mounted() {
this.dataDetail  = this.$route.query.dataDetail;
this.source  = this.$route.query.source;
this.phone = sessionMenagement.getPhone();

this.member_id = sessionMenagement.getMemberId();
this.coupon_code = localStorage.getItem('coupon_code');
console.log('红包券卡券code>>',this.coupon_code);
this.getDetails().then(res=> {
if(this.source==0){
this.exchange()
}
});
},
}
