
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Icon from 'vux/src/components/icon/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import { couponStatus } from './../config/constant'
import couponApis from './../store/apis/coupon'
import {Modules, request} from '../utils/HttpRequest/request'
import sessionMenagement from '../utils/StorageManagement'
import { ossPrefix } from './../config'
import { local } from './../utils/storage'

export default {
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Cell,
Flexbox,
FlexboxItem,
AlertModule,
Confirm,
Icon,
AlertPlugin,
Toast,
},
data() {
return {

couponStatus,
address: {},
couponDetail: {},
beforeConvert: true,
converted: false,
show: false,
couponType: 2,
status: 0,
coupon: '',
brandName: '',
validityDate: '',
startTime: '',
endTime: '',
desc: '：',
condition: '指定商品',
added: false,
pay: true,
briefTitle: '',
member_id: '',
open_id: '',
coupon_code: '',
brandIcon: '',
goods_url: [],
goods_pic:'',
showPositionValue: false,
name:'',
mobile:'',
specificAddr:'',
addr:{}
}
},
methods: {

goAddressPage(){
this.$router.push({path: '/addresslist',query:{amount_type:2}});
},
onConfirm(msg) {

this.show = false;
this.$router.push({path: '/coupons'});
},

addSite() {
console.log('添加收获地址');
this.added = true;
this.$router.push({path: '/addresslist',query:{amount_type:2}});
},
exchange() {
console.log('点击了商品券线上兑换');
this.beforeConvert=true;

},
goExchange() {
if (this.added) {
couponApis.exchangeGood(this.couponDetail.id, this.addr.id)
.then(res => {
this.payment()
}).catch(err => {
this.show = true;
console.log('商品券领取失败，请重试！', err)
this.briefTitle = '兑换失败'
this.$vux.toast.show('兑换失败')
})
} else {
AlertModule.show({
title: '提示',
content: '请先添加收货地址',
})
}
},
payment () {
const that = this
AlertModule.show({

content: `<img style="width: 100%;height: 100%"
src="${ossPrefix}btn_ok.png">
<p>恭喜您，已成功兑换！</p><p>请注意查收货品！</p>`,
onHide () {
that.$router.push({path: '/coupons'});
}
});
},

getDetails() {
const couponDetail = local.get(local.KEY_TEMP_COUPON_INFO, true);
let msg = couponDetail;
this.couponDetail = couponDetail
this.brandName = couponDetail.brand.name
this.desc = couponDetail.description
this.coupon = couponDetail.name
this.brandIcon = couponDetail.icon
this.startTime = couponDetail.active_at
this.endTime = couponDetail.expire_at
this.status = couponDetail.status
}
},
mounted() {


this.member_id = sessionMenagement.getMemberId();
this.open_id = sessionMenagement.getOpenId();
this.coupon_code = localStorage.getItem('coupon_code');
this.getDetails();
this.addr = JSON.parse(localStorage.getItem('goodsList'));
if (!this.addr) {
console.log('Yahoo')
this.added = false
} else {
this.added = true
}
}
}
