
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Icon from 'vux/src/components/icon/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { ossPrefix } from '../config'
import {Modules, request} from '../utils/HttpRequest/request'
import sessionMenagement from '../utils/StorageManagement'
import store from '../store'
import { local } from './../utils/storage'
import { couponStatus } from './../config/constant'
export default {
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Cell,
Flexbox,
FlexboxItem,
AlertModule,
Confirm,
Icon,
AlertPlugin
},
data() {
return {
couponStatus,

beforeConvert: true,
converted: false,
show: false,
couponType: 4,
status: 0,
coupon: '',
brandName: '',
validityDate: '',
startTime: '',
endTime: '',
desc: '：',
condition: '指定商品',
added: false,
pay: true,
dataDetail: '',
briefTitle: '',
member_id: '',
coupon_code: store.state.coupon_code,
brandIcon: '',
goods_url: [],
goods_pic: '',
prd_price: 0,
postage: 0,
coupon_price: 0,
totalPrice: 0,
name: '',
mobile: '',
specificAddr: '',
addr: {}
}
},
methods: {

goAddressPage() {
this.$router.push({path: '/addresslist', query: {amount_type: 4}});
},
onConfirm(msg) {
console.log('确定', msg);

this.show = false;
this.$router.push({path: '/coupons'});
},

addSite() {
console.log('添加收获地址');
this.added = true;
this.$router.push({path: '/addresslist', query: { amount_type: 4 }});
},
exchange() {
console.log('点击了抵扣券线上兑换');
this.beforeConvert = true;
},
goExchange() {
if (this.added) {
this.show = true;
this.briefTitle = '兑换成功'
} else {
AlertModule.show({
title: '提示',
content: '请先添加收货地址',
})
}
},
payment() {
if (this.added) {
AlertModule.show({

content: `<img style="width: 100%;height: 100%" src="${ossPrefix}btn_ok.png">
<p>恭喜您，已成功兑换！</p>
<p>请注意查收货品！</p>`,
});
} else {
AlertModule.show({
title: '提示',
content: '请先添加收货地址',
})
}
},

getDetails() {
this.goods_url = [];
this.totalPrice = 0;
const couponDetail = local.get(local.KEY_TEMP_COUPON_INFO, true);
let msg = couponDetail;
this.couponDetail = couponDetail
this.brandName = couponDetail.brand.name
this.desc = couponDetail.description
this.coupon = couponDetail.name
this.brandIcon = couponDetail.icon
this.startTime = couponDetail.active_at
this.endTime = couponDetail.expire_at
this.status = couponDetail.status




this.totalPrice = ((+this.prd_price) - (+this.coupon_price)).toFixed(2);
},
},
mounted() {

this.member_id = sessionMenagement.getMemberId();
this.dataDetail = this.$route.query.dataDetail;
this.coupon_code = localStorage.getItem('coupon_code');
this.getDetails();
this.addr = JSON.parse(localStorage.getItem('goodsList'));
console.log('抵扣页存储>>', this.addr);
if (this.addr) {
this.added = true;
} else {
this.added = false
}
}

}
